<template>
    <view class="main main-top-bg margin-bottom-xl">
		<view class="head ">
			<view class="flex flex-wrap padding-sm">
				<view class="basis-df">首页</view>
				<view class="basis-df text-right">
					<image src="/static/layout.png" @click="layout" style="width: 40rpx;height: 40rpx;"></image>
				</view>
			</view>
		</view>
        <view class="ad-banner ">
            <image src="/static/top_banner2.png" ></image>
        </view>
        <view class="cu-list grid" :class="['col-' + gridCol,gridBorder?'':'no-border']">
            <view class="cu-item" v-for="(item,index) in cuIconList" :key="index" v-if="index<gridCol*2" @click="workSelection" :data-id="index">
                <view :class="['cuIcon-' + item.cuIcon,'text-' + item.color]">
                    <view class="cu-tag badge" v-if="item.badge!=0">
                        <block v-if="item.badge!=1">{{item.badge>99?'99+':item.badge}}</block>
                    </view>
                </view>
                <text>{{item.name}}</text>
            </view>
						<button class="cu-btn modal-btn btn-fix" @click="clickArea" >图表</button>
        </view>
		

		<!--弹出对话框-->
		<view class="cu-modal" :class="modalName=='DialogModal1'?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar modal-title-bg justify-end">
					<view class="content">巡检任务提示</view>
					<view class="action " @tap="hideModal">
						<text class="cuIcon-close text-white"></text>
					</view>
				</view>
				<view class="padding-xl modal-content-bg">{{modalText}}</view>
				<view class="cu-bar bg-white justify-center">
					<view class="action">
						<button class="cu-btn modal-btn" @tap="hideModal">确定</button>
						<button class="cu-btn modal-btn" @tap="hideModal">取消</button>
					</view>
				</view>
			</view>
		</view>
    </view>
</template>

<script>
	import {setToken, getToken ,setInspectionModal, getByKey ,setByKey} from '../../util/auth.js'
	import {savePatrolWorkText,getDictData} from '../../api/user.js'
	import {upload} from '../../api/upload.js'
	import {isNullOrEmpty,objIsNullOrEmpty,getUUID} from '../../util/stringUtil.js'
	import {isNetwork,upLoadText,upLoadPic} from '../../util/base.js'
	import store from '../../store'
	import TOKEN_MESSAGE_CONTANTS from '../../constant/commonConstant'
    export default {
        data() {
            return {
                message: '',
                workCur: 0,
                gridCol: 4,
                gridBorder: false,
                cuIconList: [{
                    cuIcon: 'discoverfill',
                    color: 'orange',
                    badge: 0,
                    name: '风机巡检'
                }, {
                    cuIcon: 'ticket',
                    color: 'yellow',
                    badge: 0,
                    name: '物资审批'
                }, {
                    cuIcon: 'form',
                    color: 'cyan',
                    badge: 0,
                    name: '操作票管理'
                }, {
                    cuIcon: 'explore',
                    color: 'mauve',
                    badge: 0,
                    name: '检修管理'
                }, {
                    cuIcon: 'form',
                    color: 'green',
                    badge: 0,
                    name: '站内巡检'
                }
				],
				modalName: null,
				modalText:'',
				vCodeTips:'',//定时
				intervalId:'',//定时
				netWorkFlag:''//网络标识
            }
        },
        onLoad(){
			console.log("index页面加载了")
			//登录完上传缓存
			this.upLoad()
			// this.testTime()//定时
        },
		// onShow: function() {
		// 	console.log("onShow加载了")
		// },
        methods:{
					clickArea(){
						uni.navigateTo({
						    url: 'analysisChart'
						});
					},
            workSelection(e){
				clearInterval(this.intervalId)
                this.workCur = e.currentTarget.dataset.id;
                if(this.workCur==0){
                    uni.navigateTo({
                        url: 'inspectionStart'
                    });
                }
                if(this.workCur==1){
                    uni.navigateTo({
                        url: 'approvalMain'
                    });
                }
                if(this.workCur==2){
                    uni.navigateTo({
                        url: 'operationTicket'
                    });
                }
                if(this.workCur==3){
                    uni.navigateTo({
                        url: 'overhaul'
                    });
                }
				if(this.workCur==4){
				    uni.navigateTo({
				        url: 'insidePatrolList'
				    });
				}
            },
			//模态框
			showModal() {
				this.modalName = 'DialogModal1'
			},
			hideModal() {
				this.modalName = null
			},
			upLoad(){
				var _this = this
				_this.netWork()
				//加载下拉框数据
				var patrolNum = getByKey('patrolNum')
				if(isNullOrEmpty(patrolNum)){
					getDictData().then(res =>{
						patrolNum = res.patrolNum
						setByKey('patrolNum',patrolNum)
					}).catch(err => {
						console.log(err)
					});
				}
				//判断审核人信息
				var checkUser = getByKey('submitUser');
				if(isNullOrEmpty(checkUser)){
					_this.$store.dispatch('getSubmitUserList')
				}
			},
			netWork(){
				var _this = this
				var checkDetail = getByKey('checkDetail')
				var jsonData = getByKey('jsonData')
				//判断网络状态并上传
				isNetwork(function(){
					if(!isNullOrEmpty(jsonData)){
						upLoadText(jsonData)
						setByKey('jsonData',null)//清空缓存
						if(!isNullOrEmpty(checkDetail)){
							upLoadPic(checkDetail)
							setByKey('checkDetail',null)//清空缓存
						}
						_this.modalText = '未上传的巡检任务保存成功'
						_this.showModal()
						_this.netWorkFlag = true
					}
					_this.$store.dispatch('GetInsidePatrolList')//更新场内巡检任务列表信息
				},function(){
					console.log(2)
					if(!isNullOrEmpty(jsonData)){
						_this.modalText = '有待上传的巡检任务，快去联网上传'
						_this.showModal()
						_this.netWorkFlag = false
					}
				}).catch(err => err);
			},
			async layout(){
				await this.netWork()
				if(this.netWorkFlag === false){
					return
				}
				setToken(null)
				setByKey(TOKEN_MESSAGE_CONTANTS.PATROL_TEMPLATE_TYPE,null)
				uni.navigateTo({
				    url: 'index'
				});
			}
			// testTime(){
			// 	//https://ask.dcloud.net.cn/question/72540定时器
			// 	var time = 5;
			// 	var _this = this;
			// 	_this.intervalId = setInterval(function () {
			// 		time = time - 1;
			// 		_this.$nextTick(function () {
			// 		_this.vCodeTips = '重新发送(' + time + ')';
			// 		console.log(_this.vCodeTips);
			// 		})
			// 		if (time == 0) { 
			// 			time = 5;
			// 		}  
			// 	}, 1000);
			// }
        }
    }
</script>

<style>
    @import url("../../static/app.css");
    @import url("../../colorui/main.css");
    .ad-banner{
        width: 100%;
        height: 25%;
    }
    .ad-banner image{
        width: 100%;
        height: 100%;
    }
	
    uni-page-body{
        height: 100%;
				background-color: #333333;
				overflow-y: hidden;
    }
    .cu-list.grid{
        background-color: #333333;
    }
    .cu-item{
        background-color: #333333 ;
    }
    body, html{
        height: 100%;
				background-color: #333333;
				overflow-y: hidden;
    }
		.main-top-bg{
			padding-top: 20px;
			background-color: #333333;
		}
	.layoutStyle{
		width: 35px;
		height: 35px;
		margin: 5rpx;
	}
	.layoutStyle image{
		width: 100%;
		height: 100%;
	}
</style>
